ul, li{list-style:none;}

h1{text-align: center;}

.MD{margin: 0 auto; text-align: center;}
.MD ul{width: 50%; display: flex; flex-wrap: wrap; justify-content: center; margin: 0 auto;}
.MD ul li{padding: 0.5rem 1rem; background: #eee; border-radius: 2rem; margin: 0.5rem;}
.MD ul li:hover{background:rgba(219, 192, 228, 0.1); color: #5f0080; transition: 0.5s; cursor: pointer;}
.MD .interior{background: #5f0080; color: #fff;}